.wrapper{max-width:none;display:inline-block;position:relative}.wrapper:hover:after{top:0;right:0;color:#fff;background:rgba(0,0,0,.4);margin-top:-20px;margin-right:-310px;font-size:20px;position:absolute;content:"Для подтверждения нажмите Enter."}.label{display:inline-block !important;margin-right:10px}.label:hover{cursor:pointer}.input{display:inline;cursor:pointer;text-decoration:underline;width:240px}.icon{display:inline;cursor:pointer;position:relative}.icon:hover:after{top:0;right:0;color:#fff;background:rgba(0,0,0,.4);margin-top:-20px;margin-right:-100px;font-size:20px;position:absolute;content:"Отменить."}