@use '../commonStyle/mixins';
@use '../commonStyle/color';
$fontWeight: bolder;
.link {
    @include mixins.authLink(color.$buttonBlue);
}
.linkActive {
    text-decoration:underline;
    @include mixins.authLink(color.$linkActive);
